.game {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  width: 100%;
  height: 100%;
  padding: 20px;
}

.game canvas {
  pointer-events: none;
  opacity: 0;
}

.gameText {
  position: absolute;
  z-index: 1;
}

.fps {
  position: absolute;
  top: 50;
  right: 0;
  margin: 20px;
  z-index: 1;
}

.ping {
  position: absolute;
  top: -20px;
  left: -10px;
  color: peru;
  z-index: 5;
}

.offline canvas {
  opacity: 0 !important;
}

.offline .gameText {
  color: transparent !important;
}

.offline .gameText::after {
  content: "...";
  color: #00e7ff;
  position: absolute;
  left: calc(50% - 30px);
  top: -50px;
}
